.nav-container {
	position: fixed;
	top: 0;
	z-index: 100;
	width: 100%;
	height: 167px;
	transition: 0.2s ease;
	background: transparent;
	display: flex;
	justify-content: center;
	.nav-wrapper {
		position: relative;
		height: inherit;
		width: 80%;
		min-width: 60rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		.nav-symbol {
			width: 320px;
			height: 60px;
			display: flex;
			margin-right: 3.5rem;
			align-items: center;
			justify-content: space-between;
			left: 10%;
			.nav-logo {
				width: 62px;
				height: 60px;
				margin-right: 20px;
			}
			.nav-title {
				width: 239px;
				font-family: Comfortaa;
				font-style: normal;
				font-weight: bold;
				font-size: 1.125rem;
				color: #f26941;
			}
		}
		.nav-bar {
			width: 711px;
			height: 30px;
			left: 44.17%;
			display: flex;
			justify-content: space-between;
			margin-top: 5px;
			.nav-link {
				font-family: Comfortaa;
				font-style: normal;
				font-weight: 500;
				font-size: 1.125rem;
				line-height: 20px;
				color: #000000;
				text-decoration: none;
			}
			.nav-link-active {
				font-weight: bold;
				border-bottom: 2px solid #ff656a;
				color: #ff656a;
			}
		}
	}
}
.nav-scroll {
	height: 100px;
	box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
	background: rgba(255, 255, 255, 0.9);
}
