.footer-container {
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 590px;
	background: transparent;
	font-family: Avenir;
	font-style: normal;
	color: #000000;
	line-height: 36px;
	.footer-icon {
		position: absolute;
		height: 510px;
		width: 100%;
		bottom: 0;
		left: 0;
		object-fit: cover;
		object-position: top;
	}
	.footer-info-1 {
		position: absolute;
		left: calc(50% - 132px / 2 - 515px);
		top: 280px;
	}
	.footer-info-2 {
		position: absolute;
		left: calc(50% - 166px / 2 + 35px);
		top: 280px;
	}
	.footer-info-3 {
		position: absolute;
		left: calc(50% - 125px / 2 + 414.5px);
		top: 280px;
	}
	.footer-info-4 {
		position: absolute;
		left: calc(50% - 311px / 2 - 425.5px);
		top: 330px;
	}

	.footer-link {
		height: 35px;
		border-bottom: 10px;
		display: inline-block;
		box-sizing: border-box;
		text-decoration: none;
		color: #000000;
		&:hover {
			border-bottom: 2px solid #000;
		}
	}

	.nav-1 {
		position: absolute;
		width: 99px;
		height: 144px;
		left: calc(50% - 99px / 2 + 401.5px);
		top: 330px;
	}
	.nav-2 {
		position: absolute;
		width: 97px;
		height: 144px;
		left: calc(50% - 107px / 2 + 525.5px);
		top: 330px;
	}
	.footer-font-1 {
		font-weight: 500;
		font-size: 24px;
	}
	.footer-font-2 {
		font-weight: 500;
		font-size: 18px;
	}
	.footer-font-3 {
		font-weight: 800;
		font-size: 18px;
	}
	.footer-QR {
		position: absolute;
		width: 178px;
		height: 178px;
		left: calc(50% - 178px / 2 + 41px);
		top: 340px;
	}
}
