.body-container {
	margin-top: 167px;
	width: 100%;
	display: block;
	min-height: 1000px;
	section {
		margin-top: 100px;
		.center {
			margin: 0 auto;
		}
		.font {
			font-family: Avenir;
			font-style: normal;
			font-size: 1.125rem;
			font-weight: normal;
			color: #000000;
		}
		.home-title {
			text-align: center;
			font-family: Comfortaa;
			font-style: normal;
			font-weight: bold;
			font-size: 2.25rem;
			line-height: 2rem;
		}

		.section-wrapper {
			padding-top: 5%;
			display: flex;
			width: 80%;
			height: 100%;
			margin: 0 auto;
		}
	}
	#section2 {
		height: 730px;
		.section-wrapper {
			height: 100%;
			flex-direction: column;
			display: flex;
			justify-content: space-between;
			.lower-wrapper {
				display: flex;
				justify-content: space-between;
				.left-wrapper {
					width: 45%;
					.intro {
						height: 128px;
						color: #323130;
						line-height: 150%;
						text-align: justify;
					}
					.feature {
						height: 128px;
						background: #f5f5ff;
						margin-top: 10px;
						display: flex;
						align-items: center;
						justify-content: space-between;
						padding: 0 30px;
						img {
							height: 70px;
							width: 70px;
						}
						.text-wrapper {
							width: 80%;
							height: 100%;
							display: flex;
							flex-direction: column;
							justify-content: space-between;
							padding: 20px 0;
							.feature-title {
								font-weight: 800;
								font-size: 1.5rem;
								line-height: 1.5rem;
							}
							.feature-info {
								color: #555555;
								line-height: 1.6rem;
							}
						}
					}
				}
				.right-wrapper {
					width: 45%;
					img {
						width: 100%;
						height: 100%;
					}
				}
			}
		}
	}
	#section3 {
		height: auto;
		.section-wrapper {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			.program-title {
				margin-bottom: 60px;
			}
			ul {
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				justify-content: space-between;
				li {
					display: block;
					width: 45%;
					min-width: 460px;
					list-style: none;
					height: 240px;
					margin-bottom: 40px;
				}
			}
		}
	}
	#section4 {
		position: relative;
		width: 100%;
		height: 1800px;
		background: #fff3f4;
		.section4-wrapper {
			justify-content: space-between;
			.timeLine-wrapper {
				position: relative;
				width: 25%;
				.timeLine-item-wrapper {
					margin-top: 50px;
					.event-item {
						list-style: none;
						display: flex;
						justify-content: center;
					}
				}
			}
			.schoolInfo-wrapper {
				position: relative;
				width: 40%;
				display: flex;
				flex-direction: column;
				align-items: center;
				.infoCard-wrapper {
					margin-top: 50px;
					.infoCard-item {
						list-style: none;
					}
				}
				.btn {
					outline: 0;
					border: 0 none;
					height: 44px;
					width: 160px;
					background: #ff656a;
					border-radius: 100px;
					line-height: 100%;
					display: flex;
					text-align: center;
					color: #ffffff;
					font-weight: 800;
					justify-content: center;
					&:hover {
						box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
							0 17px 50px 0 rgba(0, 0, 0, 0.19);
					}
					&:focus {
						border: 0 none;
						outline: none;
					}
					&:active {
						box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.24),
							0 17px 50px 0 rgba(0, 0, 0, 0.19);
					}
				}
			}
		}
	}
}
